/* GO! */
#content {
  width: 100%;
  position: relative;
  transition: all 0s ease 2s; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.4; }

#main {
  background-color: #fff; }

#main.yukfoo, #main.home {
  background-color: #fff; }

footer {
  position: relative;
  float: left;
  height: 30px; }

h1 {
  font-family: 'novecento_normal';
  font-size: 42px;
  line-height: 1.2;
  padding-bottom: 0.3em; }

h1.maintitle {
  font-size: 36px;
  padding-top: 1em;
  color: #fff;
  padding-bottom: 1em;
  margin-bottom: 1em;
  text-shadow: 2px 2px 2px #771940; }

h1.maintitle.contacttitle {
  text-align: center; }

h2 {
  font-family: 'novecento_normal';
  font-size: 25px;
  padding-top: 0.7em;
  padding-bottom: 0.7em;
  border-top: 1px dashed rgba(0, 0, 0, 0.2); }

h3 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #333;
  font-style: italic;
  text-transform: lowercase;
  font-size: 16px;
  width: 100%;
  text-align: right;
  margin-bottom: 3em;
  padding-top: 2em;
  border-top: 2px dashed rgba(0, 0, 0, 0.1); }

p {
  font-family: 'Lora', serif;
  line-height: 1.8;
  margin-bottom: 0.5em; }

a {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  text-decoration: underline;
  cursor: pointer; }

a:hover {
  text-decoration: none; }

a[href].squarelink {
  font-family: 'Open Sans', sans-serif;
  position: absolute;
  top: 10px;
  left: 10px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  padding: 4px 6px;
  color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
  border-radius: 2px;
  line-height: 1;
  text-decoration: none;
  transition: all 0.3s ease-out; }
  @media (min-width: 1200px) {
    a[href].squarelink {
      position: fixed; } }

a[href].squarelink.contact {
  background-color: #771940;
  border-color: #771940;
  color: #fff; }

a[href].squarelink.contact.right {
  right: 10px;
  left: auto; }

a[href].squarelink.contact:hover {
  background-color: #fff;
  border-color: #771940;
  color: #771940; }

footer a[href].squarelink {
  bottom: 10px;
  top: auto; }
  @media (min-width: 1200px) {
    footer a[href].squarelink {
      display: none; } }

a[href].squarelink:hover {
  color: #000;
  border-color: #333; }

a[href].squarelink:active {
  color: #fff;
  border-color: #333;
  background-color: #333;
  transition: all 0s ease-out; }

.project-list a[href] {
  padding: 3px 6px;
  display: inline-block;
  border-radius: 2px;
  text-decoration: none;
  opacity: 0.4;
  color: #B72763;
  border: 1px solid #B72763;
  font-size: 15px;
  margin-bottom: 0.2em;
  transition: all 0.3s ease-out; }

.project-list a[href]:hover {
  opacity: 1; }

.project-list a[href]:active {
  background-color: #B72763;
  border-color: #B72763; }

